import { Link } from "react-router-dom";
import CarsList from "../sell-cars.json";
import style from "../style/CarsPage.module.css";

function CarsPage() {
  const ID = localStorage.getItem("ID");

  const oneObjOfList = CarsList[parseInt(ID) - 1];
  return (
    <div className={style.wholePage}>
      <header className={style.header}>
        <div>I hope you will like the car</div>
      </header>
      <article className={style.CarsPage}>
        <section className={style.sectionOne}>
          <div className={style.listsHeader}>
            <h1>Cars Information:</h1>
          </div>
          <div className={style.listPlace}>
            <ul className={style.list}>
              <li>
                <b>Car name:</b> {oneObjOfList.brand},
              </li>
              <li>
                <b>Made in:</b> {oneObjOfList.year},
              </li>
              <li>
                <b>Price:</b> {oneObjOfList.price}$,
              </li>
              <li>
                <b>Color:</b> {oneObjOfList.color},
              </li>
              <li>
                <b>Location:</b> {oneObjOfList.country}, {oneObjOfList.city}.
              </li>
            </ul>
          </div>
        </section>
        <section className={style.sectionTwo}>
          <div className={style.listsHeader}>
            <h1>Owners Information:</h1>
          </div>
          <div className={style.listPlace}>
            <ul>
              <li>
                <b>Owner of car:</b> {oneObjOfList.full_name},
              </li>
              <li>
                <b>His/Her mail:</b> {oneObjOfList.phone},
              </li>
              <li>
                <b>His/Her mail:</b> {oneObjOfList.mail}.
              </li>
            </ul>
          </div>
          <div></div>
        </section>
      </article>
      <div className={style.alertToUser}>
        <div>For more information speak to owner</div>
      </div>
      <footer className={style.footer}>
        <div>
          <Link to="/Cars-Shop">
            <button
              className={style.backButton}
              onClick={() => localStorage.clear()}
            >
              Back
            </button>
          </Link>
        </div>
      </footer>
    </div>
  );
}

export default CarsPage;
