import React from "react";
import CarsList from "../sell-cars.json";

/* =============== Brands Options List =================== */
const Brands = () => {
  const sortedBrandList = [];
  CarsList.map((car) => sortedBrandList.push(car.brand));
  const cleanedList = [...new Set(sortedBrandList)];
  cleanedList.sort();
  const brands = cleanedList.map((brand, index) => {
    return (
      <option value={brand} key={index + 1}>
        {brand}
      </option>
    );
  });
  return brands;
};

/* =============== Years Options List =================== */
const Years = (props) => {
  const yearsList = [];
  let counter = 0;
  for (let i = 1960; i <= 2022; i++) {
    counter++;
    yearsList.push(
      <option key={counter} value={i}>
        {i}
      </option>
    );
  }
  if (props.AOrB === "A") {
    return yearsList;
  }
  if (props.AOrB === "B") {
    return yearsList.reverse();
  }
};

/* =============== Prices Options List =================== */
const Price = (props) => {
  const priceList = [];
  let counter = 0;
  for (let i = 1000; i <= 100000; i += 200) {
    counter++;
    priceList.push(
      <option key={counter} value={i}>
        {i}
      </option>
    );
  }
  if (props.AOrB === "A") {
    return priceList;
  }
  if (props.AOrB === "B") {
    return priceList.reverse();
  }
};
function SearchSystems(props) {
  const {
    minYear,
    setMinYear,
    maxYear,
    setMaxYear,
    minPrice,
    setMinPrice,
    maxPrice,
    setMaxPrice,
    brand,
    setBrand,
  } = props;
  const InputElements = () => {
    return (
      <div className="filterInputElements">
        <div className="yearSide">
          <h2>Year</h2>
          <div>
            <div>
              <label htmlFor="minYear">
                <b>from</b>
              </label>
              <select
                name="minYear"
                id="minYear"
                defaultValue={minYear}
                onChange={(e) => setMinYear(e.target.value)}
              >
                {<Years AOrB="A" minYear />}
              </select>
            </div>
            <div>
              <label htmlFor="maxYear">
                <b>to</b>
              </label>
              <select
                name="maxYear"
                id="maxYear"
                defaultValue={maxYear}
                onChange={(e) => setMaxYear(e.target.value)}
              >
                {<Years AOrB="B" maxYear />}
              </select>
            </div>
          </div>
        </div>
        <div className="priceSide">
          <h2>Price</h2>
          <div>
            <div>
              <label htmlFor="minPrice">
                <b>from</b>
              </label>
              <select
                name="minPrice"
                id="minPrice"
                defaultValue={minPrice}
                onChange={(e) => setMinPrice(e.target.value)}
              >
                <Price AOrB="A" />
              </select>
            </div>
            <div>
              <label htmlFor="maxPrice">
                <b>to</b>
              </label>
              <select
                name="maxnPrice"
                id="maxPrice"
                defaultValue={maxPrice}
                onChange={(e) => setMaxPrice(e.target.value)}
              >
                <Price AOrB="B" />
              </select>
            </div>
          </div>
        </div>
      </div>
    );
  };
  return (
    <div className="filterComponents">
      <div
        style={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <h2>Brand</h2>
        <select
          name="Brand"
          id="brands"
          defaultValue={brand}
          onChange={(e) => setBrand(e.target.value)}
        >
          <option value="">All</option>
          <Brands />
        </select>
      </div>
      <InputElements />
    </div>
  );
}

export default SearchSystems;
