/* eslint-disable jsx-a11y/anchor-is-valid */
import "../style/global.css";
import CarsList from "../sell-cars.json";
import { Link } from "react-router-dom";
function Containers(props) {
  const { minYear, maxYear, minPrice, maxPrice, brand } = props;
  const onClickHandler = (id) => {
    localStorage.setItem("ID", id);
  };
  // eslint-disable-next-line array-callback-return
  const containersList = CarsList.map((l) => {
    const boolean =
      minYear <= l.year &&
      maxYear >= l.year &&
      minPrice <= l.price &&
      maxPrice >= l.price;
    if (boolean) {
      if (brand === "") {
        return (
          <div className="col" key={l.id}>
            <div className="header">
              <h1>{l.brand}</h1>
              <h2>({l.year})</h2>
            </div>
            <div className="information">
              <div>{`That car is in ${l.country} city ${l.city}.`}</div>
              <div>If you want to read more click on 'View' button.</div>
            </div>
            <div style={{ fontSize: "20px" }}>
              Price: <b>{l.price}$</b>
            </div>
            <div className="view-btn">
              <Link to="car">
                <button onClick={() => onClickHandler(l.id)}>View</button>
              </Link>
            </div>
          </div>
        );
      } else if (l.brand === brand) {
        return (
          <div className="col" key={l.id}>
            <div className="header">
              <h1>{l.brand}</h1>
              <h2>({l.year})</h2>
            </div>
            <div className="information">
              <div>{`That car is in ${l.country} city ${l.city}.`}</div>
              <div>If you want to read more click on 'View' button.</div>
            </div>
            <div style={{ fontSize: "20px" }}>
              Price: <b>{l.price}$</b>
            </div>
            <div className="view-btn">
              <Link to="car">
                <button onClick={() => onClickHandler(l.id)}>View</button>
              </Link>
            </div>
          </div>
        );
      }
    }
  });
  while(containersList.includes(undefined)){
    let index = containersList.findIndex(l=>l===undefined);
    containersList.splice(index,1);
  }
  return containersList.length===0?<div><h1>No Result</h1></div>:<div className="grid">{containersList}</div>;
}

export default Containers;
