import Containers from "./components/CarsContainer";
import SearchSystems from "./components/SearchSystems";
import style from "./style/App.module.css";
const Header = () => {
  return (
    <header className={style.headerOfPage}>
      <div>Find car for you or your family member</div>
    </header>
  );
};
function HomePage(props) {
    const {
        minYear,
        setMinYear,
        maxYear,
        setMaxYear,
        minPrice,
        setMinPrice,
        maxPrice,
        setMaxPrice,
        brand,
        setBrand
      } = props;
    return (  
        <>
        <Header />
        <div style={{ width: "100%", display: "flex", justifyContent: "center" }}>
        <SearchSystems
          minYear={minYear}
          setMinYear={setMinYear}
          maxYear={maxYear}
          setMaxYear={setMaxYear}
          minPrice={minPrice}
          setMinPrice={setMinPrice}
          maxPrice={maxPrice}
          setMaxPrice={setMaxPrice}
          brand={brand}
          setBrand={setBrand}
        />
      </div>
      <br />
      <br />
      <br />
      <br />
      <Containers
              minYear={minYear}
              maxYear={maxYear}
              minPrice={minPrice}
              maxPrice={maxPrice}
              brand={brand}
            />
        </>
    );
}

export default HomePage;